import React, { useContext, memo, useCallback, useMemo, useState } from 'react';
import { AppContext } from '../../providers/context';
import { useBolleanToggle } from '../../hooks';
import { LOCALES } from '../../providers/i18n';
import { saveToStorage } from '../../utils/sessionStorage';


const Test = memo(({ data }) => {
	console.log('rendering')
	return <div>{JSON.stringify(data)}</div>
})

const Settings = () => {
	const { state, dispatch } = useContext(AppContext);
	const { status, handleStatusChange } = useBolleanToggle();
	//const [isAdvancedSettingsShow, setIsAdvancedSettingsShow] = useState(false)


	const onChange = (e) => {
		const { value } = e.target;

		dispatch({
			type: 'changeCurrency',
			currency: value
		})
	}

	const onChangeLocale = (e) => {
		const { value } = e.target;
		dispatch({
			type: 'setLocale',
			locale: value
		})

		saveToStorage('locale', value)
	}


	return (<>
		<h1></h1>

		<div>
			<form>
				<div>
					<label>
						:
						<select name='currency'
							onChange={onChange}
							value={state.currency}>
							<option value={'UAH'}></option>
							<option value='USD'> </option>
							<option value='EUR'></option>
						</select>
					</label>
				</div>
				<div>
					<label>
						:
						<select name='locale'
							onChange={onChangeLocale}
							value={state.locale}>
							<option value={LOCALES.UKRAINIAN}></option>
							<option value={LOCALES.ENGLISH}>English</option>

						</select>
					</label>
				</div>

			</form>
		</div>
		<div>
			<button onClick={handleStatusChange}> </button>
			{status ?
				(<div>
					<h2> </h2>
					<p>...</p>
				</div>)
				: null}

		</div>
	</>

	);
}

export default Settings;